.chat-section__btn {
  cursor: pointer;
}

.chat-header {
  width: 100%;
  min-height: 5rem;
  background: #f2f2f2;
  border-bottom: solid 0.15rem #b9b9b9;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.chat-header .user-profile__title {
  font-size: 1.25rem;
}

.chat-section__form form {
  width: 100%;
}

.chat-user-title {
  font-size: 1.125rem;
  color: #535e95;
  font-weight: bold;
  text-transform: capitalize;
  /* margin-bottom: 0.75rem; */
}

.chat-user-title--red {
  color: #955386;
}

.chat-user-title--green {
  color: #538995;
}

.chat-section__text h4 {
  font-size: 0.85rem;
  margin-bottom: 0.75rem;
  color: rgb(121, 121, 121);
}

.chat-time {
  position: absolute;
  bottom: 0.5rem;
  right: 0.5rem;
  color: #a2a2a2;
}

/* 
.chat-section__message--received .chat-user-title {
    color: #538995;
} */

.chat-section__body .chat-section__text-body p {
  font-size: 1.125rem;
}

.chat-section__body .chat-section__text {
  box-shadow: none;
  position: relative;
}

.chat-section__body .chat-section__message--sent .chat-section__text {
  background: #fff;
}

.chat-section__body .chat-section__message--sent .chat-section__text::after {
  --length: 0.85rem;
  z-index: -1;
  content: " ";
  position: absolute;
  height: calc(var(--length) / 4);
  border-radius: 0.25rem;
  /* background: red; */
  right: calc(var(--length) * -1);
  bottom: 15%;
  border-radius: 0.5rem;
  border-top: var(--length) solid #00000000;
  border-left: calc(var(--length) * 2) solid white;
  border-bottom: var(--length) solid transparent;
}

.chat-section__body .chat-section__message--received .chat-section__text {
  background: #e2e2e2;
  /* border: #C4C4C4 solid 0.24rem; */
}

.chat-section__body
  .chat-section__message--received
  .chat-section__text::after {
  --length: 0.85rem;
  z-index: -1;
  content: " ";
  position: absolute;
  height: calc(var(--length) / 4);
  border-radius: 0.25rem;
  left: calc(var(--length) * -1);
  bottom: 15%;
  border-radius: 0.5rem;
  border-top: var(--length) solid #00000000;
  border-right: calc(var(--length) * 2) solid #e2e2e2;
  border-bottom: var(--length) solid transparent;
}

.headerMemberList {
  margin-right: 0.45rem;
  height: 2.625rem;
  width: 2.625rem;
  min-height: 2.625rem;
  min-width: 2.625rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 1.25rem;
  background: #535e95;
}

.headerMemberList-container {
  position: absolute;
  right: 0;
  display: flex;
}

#listMemberTooltip {
  background: rgba(0, 0, 0, 0.568);
  color: #fff;
  border-radius: 0.65rem;
  text-transform: capitalize;
}

