/* width */

::-webkit-scrollbar {
  width: 5px;
}

/* Track */

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.644);
}

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
  background: rgba(85, 85, 85, 0.685);
}
