.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #000;
  border-color: #000 transparent #000 transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 782px) {
  .sidebar {
    width: 100%;
  }
  .sidebar__tabs {
    width: 100%;
  }
}

.menu-notification {
  position: relative;
}

.menu-notification::after {
  content: ' ';
  width: 0.75rem;
  height: 0.75rem;
  background: #DE1B1B;
  border-radius: 50%;
  position: absolute;
  top: -0.375rem;
  right: -0.375rem;
}